export var HeaderType = {
  MEASURE: 1,
  // measure header
  DIMENSION: 2,
  // dimension value (leaf)
  DIMENSION_HEADER: 3,
  // dimension header
  GRAND_TOTAL: 4,
  SUB_TOTAL: 5
}; // Axis

export var AxisType = {
  COLUMNS: 1,
  ROWS: 2,
  MEASURE: 3,
  DIMENSION: 4,
  DATACELL: 5
};
export function toAxis(axisType) {
  switch (axisType) {
    case AxisType.COLUMNS:
      return "columns";

    case AxisType.ROWS:
      return "rows";

    case AxisType.MEASURES:
      return "measures";

    case AxisType.DIMENSION:
      return "dimensions";

    default:
      return "__AXIS_TYPE_UNKNOWN__";
  }
}
export function toAxisType(axis) {
  return AxisType[axis.toUpperCase()];
} // ACTIONS

